/*
SOLARIZED HEX     16/8 TERMCOL  XTERM/HEX   L*A*B      RGB         HSB
--------- ------- ---- -------  ----------- ---------- ----------- -----------
base03    #002b36  8/4 brblack  234 #1c1c1c 15 -12 -12   0  43  54 193 100  21
base02    #073642  0/4 black    235 #262626 20 -12 -12   7  54  66 192  90  26
base01    #586e75 10/7 brgreen  240 #585858 45 -07 -07  88 110 117 194  25  46
base00    #657b83 11/7 bryellow 241 #626262 50 -07 -07 101 123 131 195  23  51
base0     #839496 12/6 brblue   244 #808080 60 -06 -03 131 148 150 186  13  59
base1     #93a1a1 14/4 brcyan   245 #8a8a8a 65 -05 -02 147 161 161 180   9  63
base2     #eee8d5  7/7 white    254 #e4e4e4 92 -00  10 238 232 213  44  11  93
base3     #fdf6e3 15/7 brwhite  230 #ffffd7 97  00  10 253 246 227  44  10  99
yellow    #b58900  3/3 yellow   136 #af8700 60  10  65 181 137   0  45 100  71
orange    #cb4b16  9/3 brred    166 #d75f00 50  50  55 203  75  22  18  89  80
red       #dc322f  1/1 red      160 #d70000 50  65  45 220  50  47   1  79  86
magenta   #d33682  5/5 magenta  125 #af005f 50  65 -05 211  54 130 331  74  83
violet    #6c71c4 13/5 brmagenta 61 #5f5faf 50  15 -45 108 113 196 237  45  77
blue      #268bd2  4/4 blue      33 #0087ff 55 -10 -45  38 139 210 205  82  82
cyan      #2aa198  6/6 cyan      37 #00afaf 60 -35 -05  42 161 152 175  74  63
green     #859900  2/2 green     64 #5f8700 60 -20  65 133 153   0  68 100  60
*/

:root {
    --base03     : #002b36;
    --base02     : #073642;
    --base01     : #586e75;
    --base00     : #657b83;
    --base0      : #839496;
    --base1      : #93a1a1;
    --base2      : #eee8d5;
    --base3      : #fdf6e3;
    --yellow     : #b58900;
    --orange     : #cb4b16;
    --red        : #dc322f;
    --magenta    : #d33682;
    --violet     : #6c71c4;
    --blue       : #268bd2;
    --cyan       : #2aa198;
    --green      : #859900;
}

body { background-color: var(--base03); }

a:link    { color: var(--base1);   text-decoration: none; }
a:visited { color: var(--blue);    text-decoration: none; }
a:hover   { color: var(--magenta); text-decoration: none; }
a:active  { color: var(--green);   text-decoration: none; }

.sdark-bg { color: var(--base0); background-color: var(--base03); }
.sdark-fg { color: var(--base1); background-color: var(--base02); }

.slight-fg { color: var(--base01); background-color: var(--base2); }
.slight-bg { color: var(--base00); background-color: var(--base3); }

.sdark-hr,
.sdark-navbar { background-color: var(--base01); }

.sdark-spacer {
    color: var(--base03);
    background-color: var(--base03);
}

.text-sdark-spacer { color: var(--base03); }
.text-sdark-bg     { color: var(--base0); }
.text-sdark-fg     { color: var(--base1); }
.text-sdark-border { color: var(--base01); }
.text-sdark-orange { color: var(--orange); }
.text-sdark-violet { color: var(--violet); }
.text-sdark-red    { color: var(--red); }

.bg-sdark-orange  { background-color: var(--orange); }
.bg-sdark-blue    { background-color: var(--blue); }
.bg-sdark-green   { background-color: var(--green); }
.bg-sdark-magenta { background-color: var(--magenta); }
.bg-sdark-cyan    { background-color: var(--cyan); }
.bg-sdark-yellow  { background-color: var(--yellow); }
.bg-sdark-red     { background-color: var(--red); }
.bg-sdark-violet  { background-color: var(--violet); }

.border-slight-right  { border-right:  1px solid var(--base2); }
.border-slight-left   { border-left:   1px solid var(--base2); }
.border-slight-top    { border-top:    1px solid var(--base2); }
.border-slight-bottom { border-bottom: 1px solid var(--base2); }

.border-sdark-right  { border-right:  1px solid var(--base01); }
.border-sdark-left   { border-left:   1px solid var(--base01); }
.border-sdark-top    { border-top:    1px solid var(--base01); }
.border-sdark-bottom { border-bottom: 1px solid var(--base01); }

.border-sdark-bottom-orange { border-bottom: 3px solid var(--orange); }

.form-control,
.form-control:focus,
.form-control:disabled {
    color: var(--base00);
    background-color: var(--base3);
    border: none;
    box-shadow: none;
}

.input-group .input-group-text {
    color: var(--base3);
    background-color: var(--cyan);
    border: 1px solid var(--cyan);
}

.input-group>.input-group-append>.input-group-text.clear {
    color: var(--base00);
    background-color: var(--base3); 
    border: 1px solid var(--base3);
}

.input-group>.input-group-append>.input-group-text.success,
.input-group>.input-group-prepend>.input-group-text.success {
    color: var(--base3);
    background-color: var(--green);
    border-color: var(--green);
}

.input-group>.input-group-prepend>.input-group-text.error,
.input-group>.input-group-append>.input-group-text.error {
    color: var(--base3);
    background-color: var(--red);
    border-color: var(--red);
}

.input-group .input-group-text .progress {
    color: white;
    background-color: var(--violet);
    border-color: var(--violet);
}

.dropdown-menu a.dropdown-item{
    white-space: normal;
    color: var(--base00);
}

.vote-btn:hover { background-color: var(--base01); }

.btn.sdark-fg:hover,
.btn.sdark-fg:focus,
.page-btn.sdark-fg:hover {
    background-color: var(--base01);
    color: var(--base3);
}

.hover-white:hover { color: white; }
